const i18nHandler = {
    get: function (i18n: typeof I18NConfig, prop: keyof typeof I18NConfig) {
        return prop in i18n ? i18n[prop] : i18n['en'] // to return 'en' as a default i18n setting.
    }
}

export const I18N = new Proxy(I18NConfig, i18nHandler)

